    <div v-if="lang===`pl`">
      <h4 class="fw-bold" >Zwycięski wódz</h4>

      <p class="fw-bold">Gen. Władysław Anders, dowódca Armii Polskiej w ZSRR oraz 2 Korpusu Polskiego, zasłynął jako dowódca, którego
      żołnierze zdobyli w maju 1944 roku włoskie wzgórze Monte Cassino. Generał cieszył się wielkim autorytetem wśród
