      do armii rosyjskiej. Ukończył szkołę podoficerską w stopniu chorążego rezerwy i rozpoczął studia politechniczne w
      Rydze. W czasie I wojny światowej został powołany do carskiej armii, w której dowodził szwadronem jazdy. Potem
      ukończył kurs Akademii Sztabu Generalnego w Petersburgu i został wysłany na front do Rumunii jako szef sztabu jednej
        z dywizji piechoty.
      </p>
  <h4 class="fw-bold">Dla niepodległej</h4>
      <p>„Cały czas Anders myślał o walce dla Polski, dlatego po rewolucji lutowej w lipcu 1917 roku wstąpił do tworzonego na
