      </p>
  <h4 class="fw-bold">Z nieludzkiej ziemi</h4>
      <p>Po wybuchu II wojny generał na czele Nowogródzkiej Brygady Kawalerii, a potem Grupy Operacyjnej Kawalerii walczył z
