    </p>
      <p>Sytuację Andersa zmienił wybuch wojny sowiecko-niemieckiej w czerwcu 1941 roku i podpisanie 30 lipca układu
      Sikorski-Majski, który przewidywał uwolnienie Polaków z sowieckich łagrów oraz utworzenie polskiego wojska na
