      </p>
  <h4 class="fw-bold">Walka o wzgórze</h4>
      <p>Na przełomie 1943 i 1944 roku jednostki polskie zostały przetransportowane do Włoch i podporządkowane brytyjskiej 8
      Armii. Podczas kampanii włoskiej 2 KP wsławił się przede wszystkim zdobyciem silnie ufortyfikowanego wzgórza Monte
      Cassino zamykającego drogę do Rzymu.
      </p>
      <p>Kiedy gen. Oliver Leese, dowódca 8 Armii zaproponował gen. Andersowi zdobywanie przez Polaków wzgórza, polski
      dowódca zdawał sobie sprawę, że będzie to trudne zadanie, ale przyczyni się do wzmocnienia pozycji Polski wśród
      aliantów. „Wykonanie tego zadania […] byłoby najlepszą odpowiedzią na propagandę sowiecką, która twierdziła, że
      Polacy nie chcą się bić z Niemcami. Podtrzymałoby na duchu opór walczącego Kraju. Przyniosłoby dużą chwałę orężowi
      polskiemu”, zapisał gen. Anders w książce Bez ostatniego rozdziału.
      </p>
      <p>Po tygodniu ciężkich walk 18 maja 1944 roku Polacy zdobyli Monte Cassino. Potem żołnierze 2 Korpusu pod wodzą
      Andersa zdobyli także Ankonę, przełamali linię Gotów i zdobyli Bolonię. Niestety, wkład Polaków w walkę z Niemcami
      nie wpłynął na decyzje aliantów podjęte w lutym 1945 roku na konferencji w Jałcie i pozostawienie Polski w
      sowieckiej strefie wpływów.
      </p>
      <p>„Melduję, że II Korpus nie może uznać jednostronnej decyzji, oddającej Polskę i naród na łup bolszewikom. Zwróciłem
