      żołnierza ofiary krwi”, napisał gen. Anders w telegramie do prezydenta Władysława Raczkiewicza dzień po ogłoszeniu
      postanowień wielkiej trójki. Alianci odmówili jednak wycofania żołnierzy polskich z frontu, tłumacząc się względami
      operacyjnymi.
      </p>
      <p>Po zakończeniu wojny generał nadal troszczył się o swoich żołnierzy. Organizował dla nich warsztaty pracy, szkoły,
      kursy zawodowe i wyższe studia na włoskich uczelniach, aby ułatwić im start w cywilnym życiu. – Generał nie miał
      żadnych złudzeń co do rzeczywistych zamiarów władzy ludowej i postanowił pozostać na emigracji w Londynie – tłumaczy
