    <div v-else-if="lang===`en`">
      <h4 class="fw-bold" >Victorious chief</h4>

      <p class="fw-bold">General Władysław Anders, commander of the Polish Army in the USSR and the 2nd Polish Corps, became famous as a commander whose soldiers captured the Italian hill of Monte Cassino in May 1944. The general enjoyed great authority among his subordinates, he was not only a commander for them. They treated him like a father they trusted.</p>
      <p>
      "Gene. Władysław Anders was a talented soldier, a great cavalryman and a talented commander who was loved by his subordinates, as well as a politician who had no illusions about the post-war communist power in Poland, "says Dr. Piotr Jagodziński, a historian dealing with the history of the Polish Armed Forces in the West. Władysław Anders was born on August 11, 1892 in Błonie near Kutno, to a landed gentry family. After graduating from high school he joined the Russian army. He graduated from the NCO school as a reserve ensign and began his studies at the Riga Polytechnic. During World War I, he was drafted into the tsarist army, in which he commanded a cavalry squadron. Then he completed a course at the Academy of the General Staff in St. Petersburg and was sent to the front in Romania as chief of staff of one of the infantry divisions.
      </p>
  <h4 class="fw-bold">For an independent</h4>
      <p>"All the time Anders was thinking about fighting for Poland, therefore, after the February Revolution in July 1917, he joined the 1st Polish Corps in Russia, commanded by General Józef Dowbor-Muśnicki," says the historian. After the Corps was disarmed by the Germans in May 1918, he got to the country and joined the Polish army.
      </p>
      <p>He took part in the Greater Poland Uprising as chief of staff of the Greater Poland Army, and during the Polish-Bolshevik war he commanded the 15th Poznań Lancers Regiment. He showed evidence of his military talents, incl. in clashes over Berezina. For his bravery in the fight against the Bolsheviks, he was awarded the Virtuti Militari 5th class order by Marshal Józef Piłsudski.
      </p>
      <p>After the war, the young officer studied at the famous Paris Military Academy, and after his return he headed a course for senior commanders at the Military Academy in Warsaw and was the garrison commander in Warsaw. During the May coup, Colonel Anders sided with the government and organized the evacuation of President Stanisław Wojciechowski from the Belweder Palace. - Standing on the opposite side to Marshal Piłsudski, however, did not hinder his further military career - emphasizes the researcher.
      </p>
      <p>The colonel gained special recognition from the military authorities after great cavalry maneuvers in Volhynia. "Anders is an officer to whom, in the future, all, even the highest, positions in the army should be open," stated General Gustaw Orlicz-Dreszer, commander of the 2nd Cavalry Division after the exercises. Soon after, Colonel Anders became the chief of staff in the General Driving Inspectorate. Then he commanded the Cavalry Brigade in Brody, and in 1937, as a brigadier general, he took command of the Nowogródek Cavalry Brigade in Baranowicze.
      </p>
      <p>"Gene. Anders was not only a great cavalryman, but also a rider ”- adds Dr. Jagodziński. In 1932, he led the Polish team, which won four first prizes during the equestrian competition in Nice, including the most important - the Nations Cup.


      </p>
  <h4 class="fw-bold">From inhuman land</h4>
      <p>After the outbreak of World War II, the general at the head of the Navahrudak Cavalry Brigade and then the Cavalry Operational Group fought with the Germans, incl. in the vicinity of Płock, Mińsk Mazowiecki and Tomaszów Lubelski. After the attack on Poland on September 17, the Red Army, General Anders divided the troops into smaller groups and ordered them to break through to Hungary. On September 29, he himself was wounded and was captured by the Soviets near Sambor near Lviv.
      </p>
    <p>He was held, inter alia, in the central NKVD prison in Lubyanka, Moscow. "The Soviets repeatedly interrogated him and unsuccessfully persuaded him to join the Red Army, but the general strongly rejected this proposal" - noted the historian.


    </p>
      <p>Anders' situation was changed by the outbreak of the Soviet-German war in June 1941 and the signing of the Sikorski-Majski pact on July 30, which provided for the release of Poles from Soviet labor camps and the creation of a Polish army in the Soviet Union. In early August, the general was released from prison and took command of the emerging Polish army.
      </p>
      <p>Thousands of soldiers and civilians reported to the recruitment points. The general tried to provide them with weapons, uniforms and food. He also held talks with the Soviet authorities about the disappearance of Polish officers arrested in 1939 by the Soviets. He did not know their fate until April 1943, after the Germans discovered the graves of Poles in Katyn.
      </p>
      <p>Due to organizational and supply problems, the general insisted on the evacuation of the Polish Army to British-controlled Iran. “Thanks to his efforts, in March and August 1942 over 115,000 people were evacuated from the Soviet Union. Polish citizens, including approx. 70 thousand. soldiers "- lists Dr. Jagodziński.
      </p>
      <p>In Iran, the troops were renamed the Polish Army in the East. Poles underwent training in Iraq and Palestine, and in July 1943, after the merger with the Independent Carpathian Rifle Brigade, a number of approx. 50 thousand soldiers was created. soldiers of the 2nd Polish Corps, headed by General Anders. "The general enjoyed great authority among his soldiers, he was not only a commander for them, but also a father whom they trusted" - emphasizes the historian.


      </p>
  <h4 class="fw-bold">Fight for the hill</h4>
      <p>At the turn of 1943 and 1944, Polish units were transported to Italy and subordinated to the British 8th Army. During the Italian campaign of the 2nd Polish Army, he was famous primarily for conquering the strongly fortified Monte Cassino hill that closed the road to Rome.
      </p>
      <p>When General Oliver Leese, the commander of the 8th Army, offered General Anders to conquer the hill by the Poles, the Polish commander realized that it would be a difficult task, but it would contribute to strengthening Poland's position among the Allies. “Completing this task […] would be the best response to the Soviet propaganda that Poles did not want to fight the Germans. It would cheer up the resistance of the fighting country. It would bring great glory to the Polish armed forces, ”wrote General Anders in his book Without the last chapter.
      </p>
      <p>After a week of heavy fighting, on May 18, 1944, the Poles captured Monte Cassino. Then the soldiers of the 2nd Corps led by Anders also captured Ancona, broke the Goth line and captured Bologna. Unfortunately, the contribution of Poles to the fight against Germany did not affect the Allied decisions taken in February 1945 at the Yalta conference and leaving Poland in the Soviet sphere of influence.
      </p>
      <p>“I report that the II Corps cannot recognize the unilateral decision surrendering Poland and the nation to the Bolsheviks. I asked the allied authorities to withdraw the corps from the combat sections, because I have no conscience to demand a blood sacrifice from the soldier at the moment, ”wrote General Anders in a telegram to President Władysław Raczkiewicz a day after the announcement of the decisions of the big three. The Allies, however, refused to withdraw Polish soldiers from the front, justifying operational reasons.
      </p>
      <p>After the end of the war, the general continued to care for his soldiers. He organized for them work workshops, schools, vocational courses and higher education at Italian universities to help them start their civilian life. - The general had no illusions as to the real intentions of the people's government and decided to remain in exile in London - explains Dr. Jagodziński. Anyway, as early as 1946, the communist authorities deprived him of Polish citizenship, incl. for not submitting to the command of the Polish People's Army. It was only in 1989 that the government of the People's Republic of Poland repealed this resolution.
      </p>
      <p>In exile, General Anders was involved in political activities, he was, inter alia, chairman of the National Treasury, which was to support independence activities, and in the years 1946-1954 the Supreme Commander and Inspector General of the Polish Armed Forces. “For the independence emigration, Anders was an unquestionable authority. He was the hero of the fights in Italy and the man who saved thousands of Poles from Soviet labor camps, as well as a symbol of uncompromising resistance to the new government in Poland, ”says the historian.
      </p>
      <p>The general died on May 12, 1970, on the 26th anniversary of the beginning of the Polish Battle of Monte Cassino. According to his will, he was buried among his soldiers at the Polish War Cemetery in Monte Cassino. In 1995, Władysław Anders was posthumously awarded the Order of the White Eagle, the highest Polish decoration.
      </p>
    </div>

    <div v-else-if="lang===`it`">
      <h4 class="fw-bold" >Capo vittorioso</h4>

      <p class="fw-bold">Il generale Władysław Anders, comandante dell'esercito polacco in URSS e del 2 ° corpo polacco, divenne famoso come un comandante i cui soldati conquistarono la collina italiana di Monte Cassino nel maggio 1944. Il generale godeva di grande autorità tra i suoi subordinati, non era solo un comandante per loro. Lo trattavano come un padre di cui si fidavano.</p>
      <p>
      "Gene. Władysław Anders era un soldato di talento, un grande cavaliere e un comandante di talento che era amato dai suoi subordinati, nonché un politico che non aveva illusioni sul potere comunista del dopoguerra in Polonia", afferma il dottor Piotr Jagodziński, uno storico che si occupa della storia delle forze armate polacche in Occidente. Władysław Anders nacque l'11 agosto 1892 a Błonie vicino a Kutno, da una famiglia di proprietari terrieri. Dopo il diploma di scuola superiore è entrato nell'esercito russo. Si è diplomato alla scuola NCO come guardiamarina di riserva e ha iniziato i suoi studi al Politecnico di Riga. Durante la prima guerra mondiale, fu arruolato nell'esercito zarista, in cui comandava uno squadrone di cavalleria. Quindi ha completato un corso presso l'Accademia di Stato Maggiore di San Pietroburgo ed è stato inviato al fronte in Romania come capo di stato maggiore di una delle divisioni di fanteria.
      </p>
  <h4 class="fw-bold">Per un indipendente</h4>
      <p>"Per tutto il tempo Anders pensava di combattere per la Polonia, quindi, dopo la Rivoluzione di febbraio del luglio 1917, si unì al 1 ° Corpo polacco in Russia, comandato dal generale Józef Dowbor-Muśnicki", dice lo storico. Dopo che il Corpo fu disarmato dai tedeschi nel maggio 1918, arrivò nel paese e si unì all'esercito polacco.
      </p>
      <p>Ha preso parte alla rivolta della Grande Polonia come capo di stato maggiore dell'esercito della Grande Polonia e durante la guerra polacco-bolscevica ha comandato il 15 ° reggimento dei Lancieri di Poznań. Ha mostrato prove del suo talento militare, incl. negli scontri su Berezina. Per il suo coraggio nella lotta contro i bolscevichi, è stato insignito del 5 ° ordine di classe Virtuti Militari dal maresciallo Józef Piłsudski.
      </p>
      <p>Dopo la guerra, il giovane ufficiale ha studiato presso la famosa Accademia militare di Parigi, e dopo il suo ritorno ha diretto un corso per comandanti senior presso l'Accademia militare di Varsavia ed è stato comandante della guarnigione a Varsavia. Durante il colpo di stato di maggio, il colonnello Anders si è schierato con il governo e ha organizzato l'evacuazione del presidente Stanisław Wojciechowski dal palazzo Belweder. - Stare dalla parte opposta al maresciallo Piłsudski, tuttavia, non ha ostacolato la sua ulteriore carriera militare - sottolinea il ricercatore.
      </p>
      <p>Il colonnello ha ottenuto un riconoscimento speciale dalle autorità militari dopo le grandi manovre di cavalleria in Volinia. "Anders è un ufficiale al quale, in futuro, dovrebbero essere aperte tutte le posizioni, anche le più alte, nell'esercito", ha dichiarato il generale Gustaw Orlicz-Dreszer, comandante della 2a divisione di cavalleria dopo le esercitazioni. Subito dopo, il colonnello Anders divenne il capo del personale dell'Ispettorato generale della guida. Quindi comandò la Brigata di cavalleria a Brody e nel 1937, come generale di brigata, prese il comando della Brigata di cavalleria Nowogródek a Baranowicze.
      </p>
      <p>"Gene. Anders non era solo un grande cavaliere, ma anche un cavaliere" - aggiunge il dottor Jagodziński. Nel 1932, ha guidato la squadra polacca, che ha vinto quattro primi premi durante la competizione equestre di Nizza, tra cui il più importante - le Nazioni Tazza.


      </p>
  <h4 class="fw-bold">Da terra disumana</h4>
      <p>Dopo lo scoppio della seconda guerra mondiale, il generale a capo della brigata di cavalleria Navahrudak e poi il gruppo operativo di cavalleria combatterono con i tedeschi, incl. nelle vicinanze di Płock, Mińsk Mazowiecki e Tomaszów Lubelski. Dopo l'attacco alla Polonia del 17 settembre, l'Armata Rossa, il generale Anders ha diviso le truppe in gruppi più piccoli e ha ordinato loro di sfondare in Ungheria. Il 29 settembre, lui stesso è stato ferito ed è stato catturato dai sovietici vicino a Sambor vicino a Leopoli.
      </p>
    <p>È stato detenuto, tra l'altro, nella prigione centrale dell'NKVD a Lubjanka, a Mosca. "I sovietici lo interrogarono ripetutamente e senza successo lo persuasero ad entrare nell'Armata Rossa, ma il generale respinse con forza questa proposta" - ha osservato lo storico.


    </p>
      <p>La situazione di Anders fu cambiata dallo scoppio della guerra sovietico-tedesca nel giugno 1941 e dalla firma del patto Sikorski-Majski il 30 luglio, che prevedeva il rilascio dei polacchi dai campi di lavoro sovietici e la creazione di un esercito polacco nel Unione Sovietica. Ai primi di agosto, il generale è stato rilasciato dalla prigione e ha preso il comando dell'emergente esercito polacco.
      </p>
      <p>Migliaia di soldati e civili si sono presentati ai punti di reclutamento. Il generale ha cercato di fornire loro armi, uniformi e cibo. Ha anche tenuto colloqui con le autorità sovietiche sulla scomparsa di ufficiali polacchi arrestati nel 1939 dai sovietici. Non conosceva il loro destino fino all'aprile 1943, dopo che i tedeschi scoprirono le tombe dei polacchi a Katyn.
      </p>
      <p>A causa di problemi organizzativi e di approvvigionamento, il generale ha insistito sull'evacuazione dell'esercito polacco nell'Iran controllato dai britannici. “Grazie ai suoi sforzi, nel marzo e nell'agosto 1942 oltre 115.000 persone furono evacuate dall'Unione Sovietica. Cittadini polacchi, compresi ca. 70 mila. soldati "- elenca il dottor Jagodziński.
      </p>
      <p>In Iran, le truppe furono ribattezzate Esercito polacco a est. I polacchi furono addestrati in Iraq e Palestina e nel luglio 1943, dopo la fusione con la Brigata Indipendente dei Fucilieri dei Carpazi, un numero di ca. Furono creati 50mila soldati. soldati del 2 ° Corpo d'Armata Polacco, guidati dal generale Anders. "Il generale godeva di grande autorità tra i suoi soldati, non era solo un comandante per loro, ma anche un padre di cui si fidavano" - sottolinea lo storico.
      </p>
  <h4 class="fw-bold">Combatti per la collina</h4>
      <p>A cavallo tra il 1943 e il 1944, le unità polacche furono trasportate in Italia e subordinate all'8a armata britannica. Durante la campagna d'Italia della 2a Armata Polacca, era famoso principalmente per aver conquistato il colle di Monte Cassino fortemente fortificato che chiudeva la strada per Roma.
      </p>
      <p>Quando il generale Oliver Leese, il comandante dell'8a armata, offrì al generale Anders di conquistare la collina dai polacchi, il comandante polacco si rese conto che sarebbe stato un compito difficile, ma avrebbe contribuito a rafforzare la posizione della Polonia tra gli alleati. “Completare questo compito […] sarebbe la migliore risposta alla propaganda sovietica che i polacchi non volevano combattere contro i tedeschi. Rallegrerebbe la resistenza del paese combattente. Porterebbe grande gloria alle forze armate polacche ”, ha scritto il generale Anders nel suo libro Senza l'ultimo capitolo.
      </p>
      <p>Dopo una settimana di pesanti combattimenti, il 18 maggio 1944, i polacchi conquistarono Monte Cassino. Poi anche i soldati del 2 ° Corpo d'armata guidati da Anders catturarono Ancona, spezzarono la linea gotica e conquistarono Bologna. Sfortunatamente, il contributo dei polacchi alla lotta contro la Germania non ha influenzato le decisioni alleate prese nel febbraio 1945 alla conferenza di Yalta e lasciando la Polonia nella sfera di influenza sovietica.


      </p>
      <p>
“Riferisco che il II Corpo non può riconoscere la decisione unilaterale di consegnare la Polonia e la nazione ai bolscevichi. Ho chiesto alle autorità alleate di ritirare il corpo dalle sezioni di combattimento, perché al momento non ho la coscienza di chiedere un sacrificio di sangue al soldato ", ha scritto il generale Anders in un telegramma al presidente Władysław Raczkiewicz il giorno dopo l'annuncio delle decisioni dei tre grandi. Gli alleati, tuttavia, rifiutarono di ritirare i soldati polacchi dal fronte, giustificando ragioni operative.

      </p>
      <p>Dopo la fine della guerra, il generale ha continuato a prendersi cura dei suoi soldati. Ha organizzato per loro laboratori di lavoro, scuole, corsi professionali e istruzione superiore presso le università italiane per aiutarli a iniziare la loro vita civile. - Il generale non si faceva illusioni sulle reali intenzioni del governo popolare e decise di restare in esilio a Londra - spiega il dott. Jagodziński. Comunque, già nel 1946, le autorità comuniste lo privarono della cittadinanza polacca, incl. per non essersi sottomesso al comando dell'esercito popolare polacco. È stato solo nel 1989 che il governo della Repubblica popolare di Polonia ha abrogato questa risoluzione.
      </p>
      <p>In esilio, il generale Anders fu coinvolto in attività politiche, fu, tra l'altro, presidente del Tesoro nazionale, che doveva sostenere le attività di indipendenza, e negli anni 1946-1954 comandante supremo e ispettore generale delle forze armate polacche. “Per l'emigrazione indipendentista, Anders era un'autorità indiscutibile. Era l'eroe delle lotte in Italia e l'uomo che ha salvato migliaia di polacchi dai campi di lavoro sovietici, nonché un simbolo di resistenza intransigente al nuovo governo in Polonia ", dice lo storico.
      </p>
      <p>Il generale morì il 12 maggio 1970, nel 26 ° anniversario dell'inizio della battaglia polacca di Montecassino. Secondo la sua volontà, fu sepolto tra i suoi soldati al Cimitero di Guerra Polacco a Monte Cassino. Nel 1995, Władysław Anders è stato insignito postumo dell'Ordine dell'Aquila Bianca, la più alta decorazione polacca.
      </p>
